/* eslint-disable @typescript-eslint/no-explicit-any */
// lib/logger.ts
export function logDebug(msg: string, data?: any) {
  try { console.debug(`[TecDoc] ${msg}`, data ?? "") } catch {}
}
export function logInfo(msg: string, data?: any) {
  try { console.info(`[TecDoc] ${msg}`, data ?? "") } catch {}
}
export function logError(msg: string, data?: any) {
  try { console.error(`[TecDoc] ${msg}`, data ?? "") } catch {}
}
